<template>
  <div class="log-viewer-container">
    <h1>系统日志</h1>
    <p>显示系统运行过程中的日志记录</p>
    
    <div class="log-controls">
      <div class="filter-options">
        <label>日志级别筛选:</label>
        <select v-model="selectedLevel">
          <option value="all">全部</option>
          <option value="DEBUG">调试</option>
          <option value="INFO">信息</option>
          <option value="WARN">警告</option>
          <option value="ERROR">错误</option>
        </select>
      </div>
      <div class="search-options">
        <label>搜索:</label>
        <input type="text" v-model="searchTerm" placeholder="搜索日志内容...">
      </div>
      <div class="action-buttons">
        <button class="btn-refresh" @click="loadLogs">刷新</button>
        <button class="btn-clear" @click="clearLogs">清空日志</button>
        <button class="btn-export" @click="exportLogs">导出日志</button>
      </div>
    </div>
    
    <div class="log-content">
      <div class="log-stats">
        <span>共 {{ filteredLogs.length }} 条记录 (总计: {{ logs.length }} 条)</span>
      </div>
      <div class="log-list">
        <div v-if="filteredLogs.length === 0" class="no-logs">
          暂无符合条件的日志记录
        </div>
        <div 
          v-for="(log, index) in filteredLogs" 
          :key="index"
          :class="['log-item', `log-${log.level.toLowerCase()}`]"
        >
          <div class="log-header">
            <span class="log-level">{{ log.level }}</span>
            <span class="log-time">{{ formatTime(log.timestamp) }}</span>
          </div>
          <div class="log-message">{{ log.message }}</div>
          <div v-if="log.data" class="log-data">
            <details>
              <summary>详细信息</summary>
              <pre>{{ JSON.stringify(log.data, null, 2) }}</pre>
            </details>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import logger from '@/services/logger'

export default {
  name: "LogViewer",
  data() {
    return {
      logs: [],
      selectedLevel: 'all',
      searchTerm: ''
    }
  },
  computed: {
    filteredLogs() {
      let result = [...this.logs]
      
      // 按级别筛选
      if (this.selectedLevel !== 'all') {
        result = result.filter(log => log.level === this.selectedLevel)
      }
      
      // 按搜索词筛选
      if (this.searchTerm) {
        const term = this.searchTerm.toLowerCase()
        result = result.filter(log => 
          log.message.toLowerCase().includes(term) ||
          (log.data && JSON.stringify(log.data).toLowerCase().includes(term))
        )
      }
      
      // 按时间倒序排列
      return result.reverse()
    }
  },
  mounted() {
    this.loadLogs()
    // 每5秒自动刷新一次
    this.refreshInterval = setInterval(() => {
      this.loadLogs()
    }, 5000)
  },
  beforeUnmount() {
    if (this.refreshInterval) {
      clearInterval(this.refreshInterval)
    }
  },
  methods: {
    loadLogs() {
      this.logs = logger.getLogs()
    },
    clearLogs() {
      if (confirm('确定要清空所有日志记录吗？')) {
        logger.clearLogs()
        this.loadLogs()
      }
    },
    formatTime(timestamp) {
      const date = new Date(timestamp)
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
      })
    },
    exportLogs() {
      const logContent = JSON.stringify(this.logs, null, 2)
      const blob = new Blob([logContent], { type: 'application/json' })
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = `logs-${new Date().toISOString().split('T')[0]}.json`
      a.click()
      URL.revokeObjectURL(url)
    }
  }
}
</script>

